@media only screen and (min-width: 539px) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
  .header > div:first-child {
    margin-left: 1rem;
  }
  .header > div:last-child {
    margin-right: 1rem;
  }
  .header > div:first-child > p:last-child {
    margin-left: 1rem;
  }
  .header > div:last-child > p {
    margin-right: 1rem;
  }
}
@media only screen and (max-width: 539px) {
  .header {
    flex-direction: column;
    align-items: center;
  }
  .header > div:first-child,
  .header > div:last-child {
    margin-top: 5px;
    width: 90vw;
    justify-content: space-between;
  }
}
.header {
  width: 100%;
  height: 60px;
  background-color: rgb(62, 63, 58);
  color: white;
  display: flex;
  position: fixed;
  top: 0;
  z-index: 100;
}
.header > div:first-child {
  display: flex;
}
.header > div:first-child > p:first-child {
  font-size: large;
}
.header > div:first-child > p:last-child {
  position: relative;
  top: 4px;
  color: #98978b;
  font-size: 0.8rem;
  cursor: pointer;
}
.header > div:first-child > p:last-child:hover {
  color: white;
}
.header > div:last-child {
  display: flex;
}
.header > div:last-child > p {
  position: relative;
  top: 4px;
}
.header > div:last-child > button {
  padding: 5px 10px;
  border: none;
  border-radius: 10px;
  background-color: #f53848;
  color: white;
}
.header > div:last-child > button:hover {
  background-color: #8c2a2c;
}
